body {
  font-family: Arial, Helvetica, sans-serif;
}

.notification {
  font-family: monospace;
  font-size: 16px;
  padding: 8px;
  margin-top: 15px;
  margin-bottom: 20px;
  border-style: double;
  border-width: thick;
  border-radius: 10px;
  border-color: rgba(77, 252, 2, 0.75);
  width: 40%;
}

.error {
  font-family: monospace;
  font-size: 16px;
  padding: 8px;
  margin-top: 15px;
  margin-bottom: 20px;
  border-style: double;
  border-width: thick;
  border-radius: 10px;
  border-color: rgb(252, 2, 2);
  width: 40%;
}
